import React from "react";
import { withPrefix } from 'gatsby';
import Layout from "../layout/layout";
import marked from "marked";
import styles from "./blogPage.module.scss";

export default function BlogPage(props) {
    // const post = props.pageContext.post;
    const headerName = `${post.title}`;
    // const markdownAsHtml = insertImages(marked(post.markdown), post.images);
    
    return (
        <Layout pageTitle={headerName}>
            {/* <div className={styles.blogPost} dangerouslySetInnerHTML={{__html: markdownAsHtml}}></div> */}
            Hello!
        </Layout>
    );
}

function insertImages(html, images) {
    let htmlString = html;
    images.forEach(image => {
        htmlString = htmlString.replace(`src="${image}"`, `src="${withPrefix(`/blog/${image}.png`)}"`);
    });
    return htmlString;
}
